#pic{
    margin-right: 20px;
    margin-top: -20px;
    border-radius: 50%;
    width: 200px;
    height: 200px;
}

.profile{
    /* background-image: url('login.jpeg'); */
    width: 500px;
    text-align: center;
    margin-left: 30%;
    margin: auto;
    padding: 40px;
    margin-top: 5%;
    color: black;
    font-family: 'Century Gothic',sans-serif;
}
.profile h1, h3, ul{
    margin-right: 20px;
    font-family: 'Roboto'; 
    font-weight: bold;
    color: blue;
    text-indent: 50px;
    line-height: 1.3;
}

.desc{
    margin-right: 20px;
    font-family: 'Roboto'; 
    font-weight: bold;
    color: darkblue;
    text-align: center;
    line-height: 1.3;
}

